.navigation-wrapper {
    padding-top: 31px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo > img {
    width: 44px;
    height: 36px;
}

.burger-menu {
    list-style: none;
    width: 22px;
    display: flex;
    flex-direction: column;
}

.burger__line {
    border: 1px solid var(--white);
}

.burger__line:not(:last-child) {
    margin-bottom: 7px;
}

#nav {
    position: absolute;
    top: 80px;
    right: 0;
    background-color: var(--dark-pink);
    width: 154px;
    padding-top: 30px;
    padding-bottom: 32px;
    text-align: center;
    border-radius: 20px 0px 0px 20px;
    padding-top: 30px;
    padding-bottom: 32px;
}

.nav__menu {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    margin-bottom: 32px;
}

.nav__link {
    font-size: 12px;
    line-height: 18px;
    transition: all 0.3s ease;
}

.nav__link::after {
    border: 5px solid white;
}

.accent-btn {
    background-color: var(--pink);
    text-align: center;
}

.nav__btn {
    padding: 1px 11px 2px 10px;
}

@media screen and (min-width: 768px) {
    .logo > img {
        width: 190px;
        height: 54px;
    }
}

@media screen and (min-width: 1024px) {
    .burger-menu {
        display: none;
        opacity: 0;
        appearance: none;
        pointer-events: none;
    }
    #nav {
        position: static;
        background-color: transparent;
        display: flex;
        width: auto;
    }

    .nav__menu {
        flex-direction: row;
        column-gap: 40px;
        margin-right: 97px;
        margin-bottom: 0;
    }

    .nav__link,
    .nav__btn {
        font-size: 16px;
        line-height: 21px;
    }

    .nav__item::after {
        content: '';
        display: block;
        margin-top: 5px;
        border: 2.5px solid transparent;
    }

    .nav__item:hover::after {
        border-color: #59253e;
    }

    .nav__btn {
        align-self: start;
    }
}
